<template>
  <div class="login">
    <div class="logo">
      <img src="../assets/img/logo.png" alt="" />
    </div>
    <div class="form-login">
      <yd-cell-group>
        <yd-cell-item>
          <span slot="left">账号：</span>
          <yd-input slot="right" required ref="input9" v-model="name" max="20" placeholder="请输入账号"></yd-input>
        </yd-cell-item>
        <yd-cell-item>
          <span slot="left">密码：</span>
          <yd-input type="password" slot="right" required ref="input10" v-model="password" placeholder="请输入密码"></yd-input>
        </yd-cell-item>
      </yd-cell-group>
      <div class="login-btn clearfixed">
        <!-- <yd-button class="loginBtn" size="large" type="danger" color="#fff" @click.native="getCode">获取验证码</yd-button> -->
        <yd-button class="loginBtn" size="large" bgcolor="#5e1515" color="#fff" @click.native="handleClick">登 录</yd-button>
      </div>
    </div>
  </div>
</template>
<script>
  import ajax from "../ajax/ajax.js";
  import dataURL from "../server/dataUrl.js";
  import auth from "../server/auth.js";
  export default {
    data() {
      return {
        name: "",
        password: ""
      };
    },
    methods: {
      async getCode() {
        let res = await ajax.Get(dataURL.login, {
          phone: this.name
        });
        this.$dialog.toast({
          mes: "验证码发送成功",
          timeout: 1500,
          icon: "success"
        });
      },
      async handleClick() {
        const input9 = this.$refs.input9;
        const input10 = this.$refs.input10;
        if (!input9.valid) {
          let str = "";
          if (input9.errorCode == "NOT_NULL") {
            str = "账号不能为空";
          } 
          // else if (input9.errorCode == "NOT_REGEX_RULE") {
          //   str = "请输入正确的手机号码";
          // }
          this.$dialog.toast({
            mes: str,
            timeout: 1500,
            icon: "error"
          });
          return;
        }
        if (!input10.valid) {
          let str = "";
          if (input10.errorCode == "NOT_NULL") {
            str = "密码不能为空";
          }
          this.$dialog.toast({
            mes: str,
            timeout: 1500,
            icon: "error"
          });
          return;
        }

        let res = await ajax.Get(dataURL.login, {
          phone: this.name,
          password: this.password,
        });
        auth.setLogged(res.token);
        this.$router.push("/");
        console.log("登录成功");
         this.$dialog.toast({
          mes: "登录成功",
          timeout: 1500,
          icon: "success"
        });
      }
    }
  };

</script>



